.nav {
    list-style: none;
    display: flex;
    gap: 50px;
    align-items: center;
}

.nav_container {
    display: flex;
    justify-content: space-around;
    background-color: darksalmon;
    padding: 10px 30px;
    height: 40px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.nav_item {
    text-decoration: none;
    font-size: 1.3em;
}

.nav_item a{
    color: white;
}