@import url(https://fonts.googleapis.com/css?family=Exo:100);
/* Background data (Original source: https://subtlepatterns.com/grid-me/) */
/* Animations */
@-webkit-keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@-moz-keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@-o-keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@-webkit-keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
@-moz-keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
@-o-keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
@keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
/* Main styles */
body {
  text-align: center;
  /* img size is 50x50 */
  background: url("data:image/png;base64,") repeat 0 0;
  -webkit-animation: bg-scrolling-reverse 2s infinite;
  /* Safari 4+ */
  -moz-animation: bg-scrolling-reverse 2s infinite;
  /* Fx 5+ */
  -o-animation: bg-scrolling-reverse 2s infinite;
  /* Opera 12+ */
  animation: bg-scrolling-reverse 2s infinite;
  /* IE 10+ */
  -webkit-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
  -o-animation-timing-function: linear;
  animation-timing-function: linear;
}

.FramePhoto{
    display: flex;
    align-self: center;
    justify-self: center;
    margin: 20px;
}

.photo{
    width: 500px;
    transition: .2s linear;
}

.photo:hover{
    width: 800px;
}

.Frame{
    background-color: #eef8fa;
    margin: 15px 15ex;
    padding: 10px;
    text-align: center;
    border-radius: 10px;
}

.Description{
    margin: 20px;
    width: 800px;
    justify-self: center;
    align-self: center;
}

.FrameBox{
    align-self: center;
    justify-self: center;
    width: 400px;
    text-align: left;
    border-radius: 15px;
    padding: 10px;
    box-shadow: 1px 1px 5px 2px black;
}

.ButtonsFrame{
    display: flex;
    justify-content: center;
    gap:10px;
}

.btn:link,
.btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    text-shadow: 1px 1px 10px #00000090;
    background: #e9967a;
    color: white;
    transition: all .2s;
}

.btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:active {
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn-white {
    background-color: #fff;
    color: #777;
}

.btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all .4s;
}

.btn-animated {
    animation: moveInBottom 5s ease-out;
    animation-fill-mode: backwards;
}

@keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

.Back{
    position: fixed;
    margin-left: auto;
    margin-right: auto;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    z-index: -100;
    background-color: #00000020;
    width: 88%;
    height: 100%;
}

.Root{
    background-color: #00000010;
}